<template>
  <div class="building">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30 bg-fff">
        <currentTab :productList="productList"></currentTab>
        <div class="public_title">
            <barTitle :title="title"></barTitle>
            <div class="fs20 building_item">为建造行业提供专属、权威的数据定制开发，帮助企业更精准、更高效地做决策数据支撑。</div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "服务范围",
        title: "Scope of Technical Service",
        companyInfo:
          "湖南思利他网络科技有限公司成立于2017年，前身是湖南耀邦。湖南思利他网络科技有限公司 成立于2017年，前身是湖南耀邦…"
      },
      title: "建筑大数据定制服务",
      productList: [
        {
          id: 0,
          title: "建筑大数据定制服务",
          path: "/building"
        },
        {
          id: 1,
          title: "数据资讯服务",
          path: "/advice"
        },
        {
          id: 2,
          title: "网站开发服务",
          path: "/webDevelop"
        },
        {
          id: 3,
          title: "APP开发服务",
          path: "/appDevelop"
        },
        {
          id: 4,
          title: "微信公众号开发服务",
          path: "/weChatDevelop"
        },
        {
          id: 5,
          title: "小程序开发服务",
          path: "/appletDevelop"
        }
      ],
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.building /deep/ .top_bar {
    background-image: url("../assets/src/images/banner-info3.png");
}
.building {
  .building_item {
    margin-top: 38px;
    margin-bottom: 618px;
  }
}
</style>


