<template>
  <div class="topBar">
    <div class="top_bar">
      <div class="bar_left dcr bf-fff color-fff">
        <div class="fs52 f500 mb40">
          {{showList.themem}}
          <span class="fs28 ml20">{{showList.title}}</span>
        </div>
        <div class="left_info">{{showList.companyInfo}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  titleInfo: {
    type: Object,
    default: ""
  },
  data() {
    return {
        showList: "",
    };
  },
  methods:{
      getInfo() {
          this.showList = this.$parent.titleInfo;
      }
  },
  created() {
      this.getInfo();
  }
}; 
</script>
<style scoped lang="less">
@import "../styles/global";
@bg-width: 1180px;
.top_bar {
  height: 468px;
  width: 100%;
  background-image: url("../assets/src/images/banner-info1.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  .bar_left {
    width: @bg-width;
    min-width: @bg-width;
    margin: 0 auto;
    height: 100%;
    .left_info {
      font-size: 17px;
      width: 700px;
    }
  }
}
</style>


