<template>
  <div class="weChatDevelop">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30 bg-fff">
      <currentTab :productList="productList"></currentTab>
      <div class="public_title">
          <barTitle :title="title"></barTitle>
          <div class="fs20 weChatDevelop_item">微信公众号和小程序的开发能够为企业增加流量入口，拓展营销渠道，为企业宣传、业务开发拓展渠道。</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "服务范围",
        title: "Scope of Technical Service",
        companyInfo:
          "湖南思利他网络科技有限公司成立于2017年，前身是湖南耀邦。湖南思利他网络科技有限公司 成立于2017年，前身是湖南耀邦…"
      },
      title:"微信公众号开发服务",
      productList: [
        {
          id: 0,
          title: "建筑大数据定制服务",
          path: "/building"
        },
        {
          id: 1,
          title: "数据资讯服务",
          path: "/advice"
        },
        {
          id: 2,
          title: "网站开发服务",
          path: "/webDevelop"
        },
        {
          id: 3,
          title: "APP开发服务",
          path: "/appDevelop"
        },
        {
          id: 4,
          title: "微信公众号开发服务",
          path: "/weChatDevelop"
        },
        {
          id: 5,
          title: "小程序开发服务",
          path: "/appletDevelop"
        }
      ]
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.weChatDevelop /deep/ .top_bar {
  background-image: url("../assets/src/images/banner-info3.png");
}
.weChatDevelop {
  .weChatDevelop_item {
    margin-top: 38px;
    margin-bottom: 618px;
  }
}
</style>


