<template>
  <div class="smallOrder bg-fff">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30">
      <currentTab :productList="productList"></currentTab>
      <div class="current2">
        <div class="small">
          <barTitle :title="title" class="mb40"></barTitle>
          <publicProduct></publicProduct>
          <div class="fs34 item_info text-c">学员们师友夹持，相互监督、相互学习、达到每日精进</div>
          <div>
              <img :src="img" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "产品介绍",
        title: "Product Presentation",
        companyInfo:
          "湖南思利他网络科技有限公司成立于2017年，前身是湖南耀邦。湖南思利他网络科技有限公司 成立于2017年，前身是湖南耀邦…"
      },
      productList: [
        {
          id: 0,
          title: "标大大",
          path: "/productBig"
        },
        {
          id: 1,
          title: "数据定制服务",
          path: "/dataService"
        },
        {
          id: 2,
          title: "日精进小程序",
          path: "/smallOrder"
        }
      ],
      title: "日精进小程序",
      img: require("../assets/src/images/small_bg.png"),
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.smallOrder {
  .current2 {
    .small {
      width: @bg-width;
      min-width: @bg-width;
      margin: 0 auto;
      .item_info {
          margin-top: 65px;
          margin-bottom: 60px;
      }
    }
  }
  .publicProduct /deep/ .show_product_img {
      border: 1px solid #dddfe4;
    }
}
</style>


