<template>
  <div class="appDevelop">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30 bg-fff">
      <currentTab :productList="productList"></currentTab>
      <div class="public_title">
          <barTitle :title="title"></barTitle>
          <div class="fs20 appDevelop_item">因为更懂建筑业，所以我们能为建筑企业开发更专业、更符合需求的APP，真正帮助企业简化业务流程，提高工作效率。</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "服务范围",
        title: "Scope of Technical Service",
        companyInfo:
          "湖南思利他网络科技有限公司成立于2017年，前身是湖南耀邦。湖南思利他网络科技有限公司 成立于2017年，前身是湖南耀邦…"
      },
      title:"APP开发服务",
      productList: [
        {
          id: 0,
          title: "建筑大数据定制服务",
          path: "/building"
        },
        {
          id: 1,
          title: "数据资讯服务",
          path: "/advice"
        },
        {
          id: 2,
          title: "网站开发服务",
          path: "/webDevelop"
        },
        {
          id: 3,
          title: "APP开发服务",
          path: "/appDevelop"
        },
        {
          id: 4,
          title: "微信公众号开发服务",
          path: "/weChatDevelop"
        },
        {
          id: 5,
          title: "小程序开发服务",
          path: "/appletDevelop"
        }
      ]
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.appDevelop /deep/ .top_bar {
  background-image: url("../assets/src/images/banner-info3.png");
}
.appDevelop {
  .appDevelop_item {
    margin-top: 38px;
    margin-bottom: 618px;
  }
}
</style>


