<template>
    <div class="barTitle">
        <div class="title">
            <div class="fs28">{{content}}</div>
          </div>
    </div>
</template>
<script>
export default {
    props: {
    title: {
      type: String,
      default: ""
    },
  },
    data() {
        return {
            content: "",
        }
    },
    created() {
        this.content = this.$parent.title;
    }
}
</script>
<style lang="less" scoped>
@import "../styles/global";
.barTitle {
    .title {
        height: 120px;
        line-height: 120px;
        border-bottom: 1px solid #dddfe4;
      }
}
</style>


